body {
  margin: 0;
  overflow-x: hidden;
  font-family: "Arial", "Tahoma", sans-serif;
  font-size: 14px;
  font-weight: normal;
  line-height: 1.29;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
  color: #111111;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff") format("woff"), url("../fonts/oswaldregular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff") format("woff"), url("../fonts/oswaldmedium.woff2") format("woff2");
  font-weight: bold;
}
.addons__header {
  margin-top: 48px;
  margin-bottom: 33px;
}
.addons__list {
  list-style: none;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid #eaeaea;
}
.addons__item {
  padding-bottom: 16px;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  border-top: 1px solid #eaeaea;
}
.addons__name {
  margin-top: 13px;
  margin-bottom: 12px;
  width: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  text-transform: uppercase;
  line-height: 1;
}
.addons__package,
.addons__price {
  color: #444444;
}
.addons__price {
  margin-left: auto;
}
.addons__button {
  margin-top: 11px;
  width: 100%;
  text-align: center;
}
.addons__goodies {
  margin: 40px auto 33px;
  padding: 185px 55px 0;
  width: 280px;
  height: 289px;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;
  color: white;
  background-image: url("../img/icon-gift.svg"), url("../img/gift-photo-mobile@1x.jpg");
  background-color: #5eaa2f;
  background-repeat: no-repeat;
  background-position: center 65px, center;
  background-size: 56px 61px, cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
