.progress {
  display: flex;
  justify-content: center;
  align-items: baseline;
}
.progress__bar {
  position: relative;
  left: 4px;
}
.progress__bar::before {
  content: "";
  display: inline-block;
  width: 84px;
  height: 14px;
  background-color: white;
  border-radius: 6px;
  border: solid 1px #cdcdcd;
}
.progress__bar::after {
  content: "";
  position: absolute;
  top: 4px;
  left: 6px;
  width: 35px;
  height: 6px;
  background-color: #5eaa2f;
  border-radius: 3px;
}
.progress__button {
  padding: 0;
  margin-left: 20px;
  margin-right: 17px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.25;
  color: #111111;
  text-transform: uppercase;
  background: none;
  border: 0;
}
@media (min-width: 768px) {
  .progress__bar {
    left: 2px;
    top: -5px;
  }
  .progress__bar::before {
    width: 430px;
    height: 6px;
    background-color: #dbdbdb;
    border-radius: 6px;
    border: none;
  }
  .progress__bar::after {
    display: none;
  }
  .progress__button {
    position: relative;
    overflow: visible;
    margin: 0 21px;
    font-size: 20px;
    line-height: 1;
  }
  .progress__button::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -7px;
    left: 0;
    height: 1px;
    background-color: #111111;
    opacity: 0.23;
  }
  .progress__button:hover::after {
    opacity: 0.4;
  }
  .progress__button:active::after {
    display: none;
  }
  .progress__toggle {
    position: absolute;
    top: -5px;
    left: 50%;
    transform: translateX(-50%);
    width: 34px;
    height: 34px;
    background-color: white;
    border: solid 1px #cdcdcd;
    border-radius: 50%;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.15);
  }
  .progress__toggle::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #5eaa2f;
  }
  .progress__toggle:hover {
    box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.25);
  }
  .progress__toggle:active {
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.3);
    border: solid 2px #5eaa2f;
  }
}
